import { updateProject, getProjectById } from "@/app/actions/projects";
import ProjectForm from "@/components/ProjectForm";
import { cookies } from "next/headers";
import { redirect, notFound } from "next/navigation";

interface EditPageProps {
    params: Promise<{ id: string }>;
}

export default async function EditProjectPage({ params }: EditPageProps) {
    const cookieStore = await cookies();
    const session = cookieStore.get("admin_session");

    if (session?.value !== "true") {
        redirect("/admin/login");
    }

    const { id } = await params;
    const project = await getProjectById(id);

    if (!project) {
        notFound();
    }

    return (
        <main className="min-h-screen bg-black text-white p-10 flex flex-col items-center">
            <div className="w-full max-w-2xl mb-10">
                <h1 className="text-3xl font-sans font-bold uppercase mb-2">Edit Project</h1>
                <p className="text-gray-500 font-mono text-xs">Update project details.</p>
            </div>

            <div className="w-full max-w-2xl bg-white/5 p-8 border border-white/10 rounded-lg">
                <ProjectForm action={updateProject} mode="edit" initialData={project} />
            </div>
        </main>
    );
}
